@import "./helpers/normalize.scss";
@import "./helpers/smart-grid.scss";
@import "./helpers/variables.scss";
@import "./helpers/mixins.scss";
@import "./helpers/fonts.scss";
@import "../modules/header/header.scss";
@import "../modules/sidebar/sidebar.scss";
@import "../modules/footer/footer.scss";
@import "./helpers/dark.scss";
* {
  font-family: "Mukta", sans-serif;
  box-sizing: border-box;
  outline: none;
}

body {
  // background: rgb(245, 223, 202);
  background-image: linear-gradient(to right, #e9e5e1 0%, #ddc9af 100%);
  overflow-x: hidden;
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.metaColor {
  color: red;
  content: "red";
}

.peNone {
  pointer-events: none;
}

body::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

.productsContainer {
  @include row-flex();
  @include md(justify-content, center);
  @include xs(justify-content, center);
  max-width: 1400px;
  margin: 0 auto 80px;
  justify-content: center;
  @media only screen and (max-width: 700px) {
    margin-bottom: 40px;
  }
}

.item {
  @include col();
  @include size-lg(12);
  @include size-md(5);
  @include size-xs(12);
  background: white;
  margin: 30px;
  max-width: 300px;
  max-height: 670px;
  padding: 25px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  flex-basis: 250px;
  align-items: center;
  text-align: center;
  box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.25s ease;
  background-image: linear-gradient(to right, #ffffff 0%, #e2ddd6 100%);
  &:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 15px 1px rgba(116, 85, 60, 0.5);
  }
  //   @media only screen and (max-width: 768px) {
  //     margin: 30px auto;
  //   }
  &:hover > .item__img {
    transform: scale(1.2);
  }
  &__name {
    text-decoration: none;
    color: black;
    font-weight: bold;
    margin-bottom: auto;
    font-size: 20px;
    cursor: pointer;
  }
  &__img {
    max-width: 180px;
    max-height: 450px;
    margin: 10px 30px;
    border-radius: 20px;
    transition: all 0.25s ease;
    &:hover {
      transform: scale(1.05);
      //box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.5);
    }
  }
  &__price {
    font-size: 30px;
    margin: 15px;
    margin-bottom: 10px;
    color: $grey;
    &:after {
      content: " $";
    }
  }
}

.btn {
  border: 1px solid darken($color: $bc, $amount: 5);
  background: none;
  font-size: 20px;
  padding: 12px 30px;
  cursor: pointer;
  transition: all 0.25s ease;
  border-radius: 5px;
  &-solo {
    max-width: 220px;
    text-align: center;
    padding: 19px 0;
    margin: 40px 10px;
    @media only screen and (max-width: 450px) {
      font-size: 16px;
      padding: 10px;
      margin: 20px 10px 20px 0;
      max-width: 150px;
    }
  }
  &:hover {
    background: lighten($color: $grey, $amount: 45);
    transform: scale(1.05);
  }
}

.btn-active {
  padding: 12px 29px;
  border: none;
  transition: background 0.25s ease;
  background: rgb(133, 170, 133);
  transition: all 0.25s ease;
  &:hover {
    background: rgb(197, 145, 145);
  }
}

.product {
  max-width: 100%;
  margin: 100px auto 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  align-items: center;
}

.footerWrap {
  text-align: center;
  margin: 10px 0 10px;
}

////////////////////////\\
.img {
  width: 100vw;
  height: auto;
  max-height: 600px;
  min-height: 300px;
  object-fit: cover;
  &-solo {
    transition: all 0.4s ease;
    width: 100%;
    max-width: 250px;
    &:hover {
      transform: scale(1.15);
    }
  }
}

.soloItem {
  width: 100vw;
  max-width: 1200px;
  display: flex;
  margin: 100px auto 50px;
  justify-content: space-around;
  background-color: white;
  border-radius: 5px;
  padding: 100px 50px;
  box-shadow: 0px 0px 20px 0px rgb(146, 130, 119);
  background-image: linear-gradient(to right, #ffffff 0%, #ebe0d4 100%);
  @media only screen and (max-width: 1060px) {
    justify-content: space-between;
    padding: 100px 0;
    padding-left: 20px;
  }
}

.desc {
  display: flex;
  flex-direction: column;
  justify-content: start;
  max-width: 600px;
  @media only screen and (max-width: 920px) {
    max-width: 500px;
  }
  @media only screen and (max-width: 820px) {
    max-width: 400px;
    margin-left: 20px;
  }
}

.solotitle {
  margin: 0;
  color: black;
  @media only screen and (max-width: 450px) {
    font-size: 24px;
  }
}

.soloprice {
  &:after {
    content: " $";
  }
  font-size: 30px;
  margin-top: 25px;
  @media only screen and (max-width: 650px) {
    margin-top: 15px;
  }
  @media only screen and (max-width: 450px) {
    font-size: 20px;
  }
}

.stock {
  margin-top: 30px;
  font-style: italic;
  @media only screen and (max-width: 650px) {
    margin-top: 15px;
  }
}

.soloid {
  margin-top: 30px;
  @media only screen and (max-width: 650px) {
    margin-top: 15px;
  }
}

.solotext {
  margin-top: 70px;
  width: 90%;
  @media only screen and (max-width: 650px) {
    margin-top: 50px;
  }
  @media only screen and (max-width: 650px) {
    margin-top: 25px;
  }
  @media only screen and (max-width: 450px) {
    font-size: 14px;
  }
}

.hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  @media only screen and (max-width: 850px) {
    margin-top: 72px;
  }
}

.text {
  color: white;
  font-size: 3.5vw;
  &-hero {
    position: absolute;
    left: 10%;
    max-width: 45%;
  }
  &-title {
    font-size: 40px;
    text-align: center;
    max-width: 80%;
    margin: 70px auto 10px;
    text-shadow: 0px 0px 1px black;
    @media only screen and (max-width: 1000px) {
      font-size: 36px;
    }
  }
  @media only screen and (max-width: 900px) {
    font-size: 36px;
    text-align: center;
    max-width: 80%;
    width: 100%;
  }
  @media only screen and (max-width: 550px) {
    font-size: 28px;
  }
}

.form {
  margin: 30px auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.1);
  padding: 70px 15px;
  text-align: center;
  @media only screen and (min-width: 900px) {
    padding: 110px 15px;
  }
}

.input {
  text-align: center;
  border: none;
  background: transparent;
  border-bottom: 2px solid #000;
  font-size: 28px;
  padding: 5px 25px;
  max-width: 90%;
  @media only screen and (max-width: 630px) {
    font-size: 18px;
    padding: 0 15px;
  }
}

.label {
  text-align: center;
  margin: 0 auto;
  font-size: 28px;
  margin-right: 10px;
  max-width: 90%;
  display: inline-block;
  @media only screen and (max-width: 630px) {
    font-size: 18px;
  }

  @media only screen and (max-width: 355px) {
    margin-bottom: 10px !important;
  }
}

.cu {
  margin-bottom: 50px;
  margin-top: 0;
  color: rgb(39, 39, 39);
}

.ddd {
  color: white;
}

.headingText {
  margin-top: 70px;

  @media only screen and (max-width: 700px) {
    margin-top: 45px;
    margin-bottom: 20px;
  }
}
