.bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.69);
  transition: all 0.2s ease;
  opacity: 1;
  z-index: 999;
  pointer-events: none;
}

.scrollLock1 {
  overflow: hidden;
}

.shoppingCont {
  width: 90%;
  height: 90%;
  position: fixed;
  top: 50%;
  left: 50%;
  max-width: 650px;
  max-height: 900px;
  transform: translate(-50%, -50%);
  background: white;
  display: flex;
  flex-direction: column;
  border: 2px groove lighten($color: $orange, $amount: 15);
  box-shadow: 1px 1px 20px 5px rgba(59, 27, 12, 0.6);
  border-radius: 20px;
  z-index: 1000;
  transition: all 0.5s ease;
  pointer-events: all !important;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 70px;
    overflow: auto;

    -ms-overflow-style: none;
    /* Internet Explorer 10+ */
    scrollbar-width: none;
    /* Firefox */
    height: 100%;
    background-image: linear-gradient(to right, #ffffff 0%, #faf3ee 100%);
  }
  &__inner::-webkit-scrollbar {
    display: none;
    /* Safari and Chrome */
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: darken($color: $grey, $amount: 15);

    background-image: linear-gradient(to bottom, #83786c 0%, #ada49f 100%);
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    box-sizing: border-box;
    position: fixed;
    width: 100%;
    z-index: 99;
  }
  &__buy {
    padding: 10px 20px;
    font-size: 36px;
    text-align: center;
    background: lighten($color: $orange, $amount: 10);
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    color: white;
    cursor: pointer;
    font-weight: bold;
    transition: background 0.25s ease;
    margin-top: auto;
    &:hover {
      background: darken($color: $orange, $amount: 0);
    }
  }
}

.shoppingHidden {
  top: -100%;
  transition: all 0.5s ease;
}

.bgHidden {
  transition: all 0.3s ease;
  opacity: 0;
  z-index: -1;
}

.title {
  font-size: 50px;
  margin: 10px;
}

.close,
.title {
  font-size: 50px;
  margin: 10px 25px;
  color: white;
  box-sizing: border-box;
}

.close {
  cursor: pointer;
  margin-top: 5px;
}

.amount {
  font-size: 50px;
  margin: 40px;
  color: $orange;
  text-align: center;
  &:before {
    content: "Amount: ";
    color: black;
  }
  &:after {
    content: "$";
  }
}

.shoppingItem {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  justify-content: center;
  align-items: center;
  border: 2px solid lighten($color: $orange, $amount: 20);
  min-width: 90%;
  position: relative;
  max-width: 240px;
  padding: 20px;
  border-radius: 15px;
  text-align: center;
  &:last-child {
    margin-bottom: 40px;
  }
  &__name {
    font-weight: bold;
    margin-bottom: auto;
    font-size: 18px;
    cursor: pointer;
  }
  &__img {
    max-width: 80px;
    max-height: 250px;
    margin: 10px 30px;
    border-radius: 20px;
  }
  &__remove {
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px 15px;
    font-size: 24px;
    cursor: pointer;
    font-weight: bold;
    @media only screen and (max-width: 600px) {
      margin: 3px 6px;
    }
  }
  &__price {
    font-size: 30px;
    margin: 15px;
    margin-bottom: 10px;
    color: $grey;
    &:after {
      content: " $";
    }
  }
}
