.whiteMode {
  color: black;
  background: rgb(233, 233, 233);
  box-shadow: 1px 1px 3px 1px rgb(206, 206, 206);
  transition: all 0.5s ease;
  &:hover {
    background: rgb(255, 255, 255);
    transform: scale(1.1);
    box-shadow: 1px 1px 8px 2px rgb(165, 165, 165);
  }
}

.darkBody {
  background-image: linear-gradient(to right, #332e2c 0%, #141312 100%);
  transition: all 0.5s ease;
}

.darkItem {
  background-image: linear-gradient(
    to right,
    #181818 0%,
    #1f1e1e 100%
  ) !important;
  color: white;
  transition: all 0.5s ease;
  &:hover {
    box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.5) !important;
  }
  &__name {
    color: white !important;
    transition: all 0.5s ease;
  }
  &__price {
    color: rgb(233, 233, 233) !important;
    transition: all 0.5s ease;
  }
}

.darkBtn {
  color: white !important;
  transition: all 0.5s ease;
  &:hover {
    color: black !important;
  }
}

.darkNav {
  background: rgb(32, 31, 31) !important;
  color: white;
  transition: all 0.5s ease;
}

.darkSoloItem {
  background-image: linear-gradient(
    to right,
    #1d1d1d 0%,
    #22201f 100%
  ) !important;
  box-shadow: 0px 0px 15px 0px rgb(14, 14, 14) !important;
}

.darkMenuItems__item1 {
  color: white !important;
  transition: color 0.5s ease;
}

.darkMenuItems__item {
  color: white !important;
  transition: color 0.5s ease;
  &:hover {
    color: rgb(255, 94, 0) !important;
  }
}

.darkShoppingCont__header {
  background: rgb(32, 32, 32);
}

.darkShoppingCont__inner {
  background-image: linear-gradient(to right, #303030 0%, #252322 100%);
}

.darkShoppingItem {
  border: 2px solid lighten($color: $orange, $amount: 5);
  &__name {
    color: white;
  }
  &__remove {
    color: white;
  }
  &__price {
    color: white;
  }
}

.darkAmount {
  &:before {
    color: white;
  }
}

.darkFooter {
  color: rgb(182, 182, 182);
}

.darkLogo {
  color: white;
  transition: all 0.25s ease;
  &:hover {
    color: rgb(65, 62, 61);
  }
}

.darkCartCount__number {
  &:before {
    color: rgb(255, 255, 255);
  }
}

@media only screen and (max-width: 850px) {
  .darkBurger:before,
  .darkBurger:after,
  .darkBurger span {
    content: "";
    background-color: rgb(253, 244, 244);
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 90px;
  }
}

.darkInput {
  border-color: rgb(196, 196, 196) !important;
  color: rgb(226, 226, 226) !important;
}

.darkForm {
  background-image: linear-gradient(
    to top,
    #141414 0%,
    #1b1b1b 100%
  ) !important;
}

.darkLabel {
  color: rgb(218, 218, 218);
}
