.header {
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.2);
  background: rgb(255, 255, 255);
  z-index: 998;
}

.logoCont {
  padding: 10px 0;
  z-index: 998;
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: darken($color: $grey, $amount: 20);
  z-index: 9;
  text-align: center;
  align-items: center;
  transition: all 0.3s ease;
  background: $orange;
  padding: 5px 15px;
  border-radius: 5px;
  &:hover {
    color: white;
    @media only screen and (min-width: 850px) {
      padding: 15px 15px;
    }
  }
}

.menuItems {
  display: flex;
  list-style: none;
  position: relative;
  transition: none;
  &__item {
    text-decoration: none;
    font-size: 25px;
    color: black;
    transition: color 0.25s ease;
    margin-left: 30px;
    padding: 13px 0;
    cursor: pointer;
    // &:hover:after {
    //   content: "";
    //   position: absolute;
    //   height: 2px;
    //   width: 100%;
    //   left: 50%;
    //   bottom: -70%;
    //   transform: translate(-50%, 0);
    //   background: $orange;
    // }
    &:hover {
      color: $orange;
    }
  }
}

.cartCount {
  position: absolute;
  float: left;
  &__number {
    color: $orange;
    &:before {
      content: ": 3";
      color: rgb(0, 0, 0);
    }
  }
  .menuItems__item-cart:hover &__number:before {
    color: $orange;
  }
}

.menuItems__item-cart {
  margin-right: 60px;
  &:after {
    content: " 2";
    color: rgb(231, 83, 25);
  }
}

@media only screen and (max-width: 850px) {
  body.scrollLock {
    overflow: hidden;
  }
  .cartCount {
    .menuItems__item-cart:hover &__number:before {
      color: orange;
    }
  }
  .logo.active2 {
    color: white;
    &:hover {
      color: darken($color: $grey, $amount: 20);
    }
  }
  .burger {
    display: block;
    position: relative;
    width: 35px;
    height: 25px;
    z-index: 9;
    margin-left: 50px;
    cursor: pointer;
  }
  .burger.active:before {
    transform: rotate(45deg);
    top: 12px;
  }
  .burger.active:after {
    transform: rotate(-45deg);
    bottom: 10px;
  }
  .burger.active span {
    transform: scale(0);
  }
  .burger:before,
  .burger:after,
  .burger span {
    content: "";
    background-color: black;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 90px;
  }
  .burger span {
    top: 11px;
    transition: all 0.15s ease;
  }
  .burger:before {
    top: 0;
    transition: all 0.25s ease;
  }
  .burger:after {
    bottom: 0;
    transition: all 0.25s ease;
  }
  .logoCont {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding: 10px 15px;
    border: none;
    z-index: 99;
    background: rgb(255, 255, 255);
    box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.2);
    width: 100%;
    transition: background 0.6s ease;
  }
  .logoCont.colorActive {
    background: white;
  }
  .menu {
    position: fixed;
    top: -200%;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(238, 238, 238);
    padding-top: 40px;
    z-index: 3;
    display: flex;
    justify-content: center;
    transition: all 0.5s ease;
    overflow: auto;
  }
  .menu.active {
    top: 0;
  }
  .menuItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    align-items: center;
    margin-top: 30px;
    li {
      margin-top: 40px;
      &:last-child {
        margin-bottom: 40px;
      }
    }
    &__item {
      position: relative;
      color: rgb(15, 15, 15);
      font-size: 30px;
      margin: 0 auto;
      min-width: 100%;
      padding: 7px 60px;
      border-radius: 50px;
      transition: background 0.25s ease;
      &:hover {
        border-bottom: none;
        background-color: rgb(36, 36, 36);
      }
    }
  }
  .darkMenu {
    background: darken($color: $grey, $amount: 20) !important;
  }
  .darkMenuItems__item {
    color: white !important;
    transition: background 0.25s ease;
    &:hover {
      border-bottom: none;
      background-color: rgb(230, 230, 230);
      color: $orange !important;
    }
  }
  .fix1 {
    padding-right: 90px;
  }
  .fix2 {
    padding-right: 105px;
  }
  .fix3 {
    padding-right: 120px;
  }
}

.selected {
  // color: $orange;
}

.darkMode {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 40px;
  color: white;
  background: rgb(43, 38, 36);
  padding: 24px 15px;
  display: flex;
  max-width: 80px;
  max-height: 80px;
  text-align: center;
  cursor: pointer;
  border-radius: 50px;
  transition: all 0.3s ease !important;
  font-size: 16px;
  box-shadow: 1px 1px 5px 1px black;
  z-index: 999;

  @media only screen and (max-width: 767px) {
    transform: scale(0.8);
    margin: 10px;
    &:hover {
      //  background: rgb(0, 0, 0);
      transform: scale(0.85);
      // box-shadow: 1px 1px 8px 2px black;
    }
  }

  @media only screen and (max-width: 450px) {
    transform: scale(0.7);
    margin: 5px;
    margin-bottom: 10px;
    &:hover {
      //  background: rgb(0, 0, 0);
      transform: scale(0.75);
      // box-shadow: 1px 1px 8px 2px black;
    }
  }
  &:hover {
    background: rgb(0, 0, 0);
    transform: scale(1.1);
    box-shadow: 1px 1px 8px 2px black;
  }
}
