html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
menu,
ol,
pre,
ul,
figure,
dl,
dd,
body,
blockquote {
  margin: 0;
  padding: 0;
}

body {
  color: black;
}

a {
  color: black;
}

a:visited:active {
  color: black;
}

input,
button {
  background-color: white;
  border: 1px solid black;
}

.header {
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.2);
  background: white;
  z-index: 998;
}

.logoCont {
  padding: 10px 0;
  z-index: 998;
}

.logo {
  text-decoration: none;
  font-size: 40px;
  color: #313131;
  z-index: 9;
  text-align: center;
  align-items: center;
  transition: all 0.3s ease;
  background: #ff5e00;
  padding: 5px 15px;
  border-radius: 5px;
}

.logo:hover {
  color: white;
}

.menuItems {
  display: flex;
  list-style: none;
  position: relative;
  transition: none;
}

.menuItems__item {
  text-decoration: none;
  font-size: 25px;
  color: black;
  transition: color 0.25s ease;
  margin-left: 30px;
  padding: 13px 0;
  cursor: pointer;
}

.menuItems__item:hover {
  color: #ff5e00;
}

.cartCount {
  position: absolute;
  float: left;
}

.cartCount__number {
  color: #ff5e00;
}

.cartCount__number:before {
  content: ": 3";
  color: black;
}

.menuItems__item-cart:hover .cartCount__number:before {
  color: #ff5e00;
}

.menuItems__item-cart {
  margin-right: 60px;
}

.menuItems__item-cart:after {
  content: " 2";
  color: #e75319;
}

.darkMode {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 40px;
  color: white;
  background: #2b2624;
  padding: 24px 15px;
  display: flex;
  max-width: 80px;
  max-height: 80px;
  text-align: center;
  cursor: pointer;
  border-radius: 50px;
  transition: all 0.3s ease !important;
  font-size: 16px;
  box-shadow: 1px 1px 5px 1px black;
  z-index: 999;
}

.darkMode:hover {
  background: black;
  transform: scale(1.1);
  box-shadow: 1px 1px 8px 2px black;
}

.bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.69);
  transition: all 0.2s ease;
  opacity: 1;
  z-index: 999;
  pointer-events: none;
}

.scrollLock1 {
  overflow: hidden;
}

.shoppingCont {
  width: 90%;
  height: 90%;
  position: fixed;
  top: 50%;
  left: 50%;
  max-width: 650px;
  max-height: 900px;
  transform: translate(-50%, -50%);
  background: white;
  display: flex;
  flex-direction: column;
  border: 2px groove #ff8e4d;
  box-shadow: 1px 1px 20px 5px rgba(59, 27, 12, 0.6);
  border-radius: 20px;
  z-index: 1000;
  transition: all 0.5s ease;
  pointer-events: all !important;
}

.shoppingCont__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 70px;
  overflow: auto;
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
  height: 100%;
  background-image: linear-gradient(to right, #ffffff 0%, #faf3ee 100%);
}

.shoppingCont__inner::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

.shoppingCont__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #3e3e3e;
  background-image: linear-gradient(to bottom, #83786c 0%, #ada49f 100%);
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  z-index: 99;
}

.shoppingCont__buy {
  padding: 10px 20px;
  font-size: 36px;
  text-align: center;
  background: #ff7e33;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.25s ease;
  margin-top: auto;
}

.shoppingCont__buy:hover {
  background: #ff5e00;
}

.shoppingHidden {
  top: -100%;
  transition: all 0.5s ease;
}

.bgHidden {
  transition: all 0.3s ease;
  opacity: 0;
  z-index: -1;
}

.title {
  font-size: 50px;
  margin: 10px;
}

.close,
.title {
  font-size: 50px;
  margin: 10px 25px;
  color: white;
  box-sizing: border-box;
}

.close {
  cursor: pointer;
  margin-top: 5px;
}

.amount {
  font-size: 50px;
  margin: 40px;
  color: #ff5e00;
  text-align: center;
}

.amount:before {
  content: "Amount: ";
  color: black;
}

.amount:after {
  content: "$";
}

.shoppingItem {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  justify-content: center;
  align-items: center;
  border: 2px solid #ff9e66;
  min-width: 90%;
  position: relative;
  max-width: 240px;
  padding: 20px;
  border-radius: 15px;
  text-align: center;
}

.shoppingItem:last-child {
  margin-bottom: 40px;
}

.shoppingItem__name {
  font-weight: bold;
  margin-bottom: auto;
  font-size: 18px;
  cursor: pointer;
}

.shoppingItem__img {
  max-width: 80px;
  max-height: 250px;
  margin: 10px 30px;
  border-radius: 20px;
}

.shoppingItem__remove {
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px 15px;
  font-size: 24px;
  cursor: pointer;
  font-weight: bold;
}

.shoppingItem__price {
  font-size: 30px;
  margin: 15px;
  margin-bottom: 10px;
  color: #646464;
}

.shoppingItem__price:after {
  content: " $";
}

.whiteMode {
  color: black;
  background: #e9e9e9;
  box-shadow: 1px 1px 3px 1px #cecece;
  transition: all 0.5s ease;
}

.whiteMode:hover {
  background: white;
  transform: scale(1.1);
  box-shadow: 1px 1px 8px 2px #a5a5a5;
}

.darkBody {
  background-image: linear-gradient(to right, #332e2c 0%, #141312 100%);
  transition: all 0.5s ease;
}

.darkItem {
  background-image: linear-gradient(to right, #181818 0%, #1f1e1e 100%) !important;
  color: white;
  transition: all 0.5s ease;
}

.darkItem:hover {
  box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.5) !important;
}

.darkItem__name {
  color: white !important;
  transition: all 0.5s ease;
}

.darkItem__price {
  color: #e9e9e9 !important;
  transition: all 0.5s ease;
}

.darkBtn {
  color: white !important;
  transition: all 0.5s ease;
}

.darkBtn:hover {
  color: black !important;
}

.darkNav {
  background: #201f1f !important;
  color: white;
  transition: all 0.5s ease;
}

.darkSoloItem {
  background-image: linear-gradient(to right, #1d1d1d 0%, #22201f 100%) !important;
  box-shadow: 0px 0px 15px 0px #0e0e0e !important;
}

.darkMenuItems__item1 {
  color: white !important;
  transition: color 0.5s ease;
}

.darkMenuItems__item {
  color: white !important;
  transition: color 0.5s ease;
}

.darkMenuItems__item:hover {
  color: #ff5e00 !important;
}

.darkShoppingCont__header {
  background: #202020;
}

.darkShoppingCont__inner {
  background-image: linear-gradient(to right, #303030 0%, #252322 100%);
}

.darkShoppingItem {
  border: 2px solid #ff6e1a;
}

.darkShoppingItem__name {
  color: white;
}

.darkShoppingItem__remove {
  color: white;
}

.darkShoppingItem__price {
  color: white;
}

.darkAmount:before {
  color: white;
}

.darkFooter {
  color: #b6b6b6;
}

.darkLogo {
  color: white;
  transition: all 0.25s ease;
}

.darkLogo:hover {
  color: #413e3d;
}

.darkCartCount__number:before {
  color: white;
}

.darkInput {
  border-color: #c4c4c4 !important;
  color: #e2e2e2 !important;
}

.darkForm {
  background-image: linear-gradient(to top, #141414 0%, #1b1b1b 100%) !important;
}

.darkLabel {
  color: #dadada;
}

* {
  font-family: "Mukta", sans-serif;
  box-sizing: border-box;
  outline: none;
}

body {
  background-image: linear-gradient(to right, #e9e5e1 0%, #ddc9af 100%);
  overflow-x: hidden;
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

.metaColor {
  color: red;
  content: "red";
}

.peNone {
  pointer-events: none;
}

body::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

.productsContainer {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  max-width: 1400px;
  margin: 0 auto 80px;
  justify-content: center;
}

.item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  background: white;
  margin: 30px;
  max-width: 300px;
  max-height: 670px;
  padding: 25px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  flex-basis: 250px;
  align-items: center;
  text-align: center;
  box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.25s ease;
  background-image: linear-gradient(to right, #ffffff 0%, #e2ddd6 100%);
}

.item:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 15px 1px rgba(116, 85, 60, 0.5);
}

.item:hover > .item__img {
  transform: scale(1.2);
}

.item__name {
  text-decoration: none;
  color: black;
  font-weight: bold;
  margin-bottom: auto;
  font-size: 20px;
  cursor: pointer;
}

.item__img {
  max-width: 180px;
  max-height: 450px;
  margin: 10px 30px;
  border-radius: 20px;
  transition: all 0.25s ease;
}

.item__img:hover {
  transform: scale(1.05);
}

.item__price {
  font-size: 30px;
  margin: 15px;
  margin-bottom: 10px;
  color: #646464;
}

.item__price:after {
  content: " $";
}

.btn {
  border: 1px solid #d9d9d9;
  background: none;
  font-size: 20px;
  padding: 12px 30px;
  cursor: pointer;
  transition: all 0.25s ease;
  border-radius: 5px;
}

.btn-solo {
  max-width: 220px;
  text-align: center;
  padding: 19px 0;
  margin: 40px 10px;
}

.btn:hover {
  background: #d7d7d7;
  transform: scale(1.05);
}

.btn-active {
  padding: 12px 29px;
  border: none;
  transition: background 0.25s ease;
  background: #85aa85;
  transition: all 0.25s ease;
}

.btn-active:hover {
  background: #c59191;
}

.product {
  max-width: 100%;
  margin: 100px auto 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  align-items: center;
}

.footerWrap {
  text-align: center;
  margin: 10px 0 10px;
}

.img {
  width: 100vw;
  height: auto;
  max-height: 600px;
  min-height: 300px;
  object-fit: cover;
}

.img-solo {
  transition: all 0.4s ease;
  width: 100%;
  max-width: 250px;
}

.img-solo:hover {
  transform: scale(1.15);
}

.soloItem {
  width: 100vw;
  max-width: 1200px;
  display: flex;
  margin: 100px auto 50px;
  justify-content: space-around;
  background-color: white;
  border-radius: 5px;
  padding: 100px 50px;
  box-shadow: 0px 0px 20px 0px #928277;
  background-image: linear-gradient(to right, #ffffff 0%, #ebe0d4 100%);
}

.desc {
  display: flex;
  flex-direction: column;
  justify-content: start;
  max-width: 600px;
}

.solotitle {
  margin: 0;
  color: black;
}

.soloprice {
  font-size: 30px;
  margin-top: 25px;
}

.soloprice:after {
  content: " $";
}

.stock {
  margin-top: 30px;
  font-style: italic;
}

.soloid {
  margin-top: 30px;
}

.solotext {
  margin-top: 70px;
  width: 90%;
}

.hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}

.text {
  color: white;
  font-size: 3.5vw;
}

.text-hero {
  position: absolute;
  left: 10%;
  max-width: 45%;
}

.text-title {
  font-size: 40px;
  text-align: center;
  max-width: 80%;
  margin: 70px auto 10px;
  text-shadow: 0px 0px 1px black;
}

.form {
  margin: 30px auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: white;
  box-shadow: 1px 1px 15px 1px rgba(0, 0, 0, 0.1);
  padding: 70px 15px;
  text-align: center;
}

.input {
  text-align: center;
  border: none;
  background: transparent;
  border-bottom: 2px solid #000;
  font-size: 28px;
  padding: 5px 25px;
  max-width: 90%;
}

.label {
  text-align: center;
  margin: 0 auto;
  font-size: 28px;
  margin-right: 10px;
  max-width: 90%;
  display: inline-block;
}

.cu {
  margin-bottom: 50px;
  margin-top: 0;
  color: #272727;
}

.ddd {
  color: white;
}

.headingText {
  margin-top: 70px;
}

@media only screen and (min-width: 850px) {
  .logo:hover {
    padding: 15px 15px;
  }
}

@media only screen and (min-width: 900px) {
  .form {
    padding: 110px 15px;
  }
}

@media screen and (max-width: 1100px) {
  .item {
    width: calc(100% - 30px);
  }
}

@media only screen and (max-width: 1060px) {
  .soloItem {
    justify-content: space-between;
    padding: 100px 0;
    padding-left: 20px;
  }
}

@media only screen and (max-width: 1000px) {
  .text-title {
    font-size: 36px;
  }
}

@media screen and (max-width: 960px) {
  .productsContainer {
    justify-content: center;
  }

  .item {
    width: calc(41.66667% - 30px);
  }
}

@media only screen and (max-width: 920px) {
  .desc {
    max-width: 500px;
  }
}

@media only screen and (max-width: 900px) {
  .text {
    font-size: 36px;
    text-align: center;
    max-width: 80%;
    width: 100%;
  }
}

@media only screen and (max-width: 850px) {
  body.scrollLock {
    overflow: hidden;
  }

  .menuItems__item-cart:hover .cartCount__number:before {
    color: orange;
  }

  .logo.active2 {
    color: white;
  }

  .logo.active2:hover {
    color: #313131;
  }

  .burger {
    display: block;
    position: relative;
    width: 35px;
    height: 25px;
    z-index: 9;
    margin-left: 50px;
    cursor: pointer;
  }

  .burger.active:before {
    transform: rotate(45deg);
    top: 12px;
  }

  .burger.active:after {
    transform: rotate(-45deg);
    bottom: 10px;
  }

  .burger.active span {
    transform: scale(0);
  }

  .burger:before,
  .burger:after,
  .burger span {
    content: "";
    background-color: black;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 90px;
  }

  .burger span {
    top: 11px;
    transition: all 0.15s ease;
  }

  .burger:before {
    top: 0;
    transition: all 0.25s ease;
  }

  .burger:after {
    bottom: 0;
    transition: all 0.25s ease;
  }

  .logoCont {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding: 10px 15px;
    border: none;
    z-index: 99;
    background: white;
    box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.2);
    width: 100%;
    transition: background 0.6s ease;
  }

  .logoCont.colorActive {
    background: white;
  }

  .menu {
    position: fixed;
    top: -200%;
    left: 0;
    width: 100%;
    height: 100%;
    background: #eeeeee;
    padding-top: 40px;
    z-index: 3;
    display: flex;
    justify-content: center;
    transition: all 0.5s ease;
    overflow: auto;
  }

  .menu.active {
    top: 0;
  }

  .menuItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    align-items: center;
    margin-top: 30px;
  }

  .menuItems li {
    margin-top: 40px;
  }

  .menuItems li:last-child {
    margin-bottom: 40px;
  }

  .menuItems__item {
    position: relative;
    color: #0f0f0f;
    font-size: 30px;
    margin: 0 auto;
    min-width: 100%;
    padding: 7px 60px;
    border-radius: 50px;
    transition: background 0.25s ease;
  }

  .menuItems__item:hover {
    border-bottom: none;
    background-color: #242424;
  }

  .darkMenu {
    background: #313131 !important;
  }

  .darkMenuItems__item {
    color: white !important;
    transition: background 0.25s ease;
  }

  .darkMenuItems__item:hover {
    border-bottom: none;
    background-color: #e6e6e6;
    color: #ff5e00 !important;
  }

  .fix1 {
    padding-right: 90px;
  }

  .fix2 {
    padding-right: 105px;
  }

  .fix3 {
    padding-right: 120px;
  }

  .darkBurger:before,
  .darkBurger:after,
  .darkBurger span {
    content: "";
    background-color: #fdf4f4;
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 99;
    left: 0;
    border-radius: 90px;
  }

  .hero {
    margin-top: 72px;
  }
}

@media only screen and (max-width: 820px) {
  .desc {
    max-width: 400px;
    margin-left: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .darkMode {
    transform: scale(0.8);
    margin: 10px;
  }

